import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReqFormComponent } from './req-form.component';
import {SharedModule} from '../shared/shared.module';
import {MatListModule, MatStepperModule} from '@angular/material';
import {FormsModule} from '@angular/forms';
import {reqFormRouting} from './req-form.routing';
import {FormService} from '../services/form.service';

@NgModule({
  imports: [
    CommonModule,
    reqFormRouting,
    SharedModule,
    FormsModule,
    MatStepperModule,
    MatListModule
  ],
  exports: [ReqFormComponent],
  declarations: [ReqFormComponent],
  providers: [
    FormService
  ]
})
export class ReqFormModule { }



