import { RouterModule, Routes } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import {ReqFormComponent} from './req-form.component';

const reqFormRoutes: Routes = [
    {
        path: '', component: ReqFormComponent
    }
];

export const reqFormRouting: ModuleWithProviders = RouterModule.forChild(reqFormRoutes);



