import { Component, OnInit } from '@angular/core';
import {FormService} from '../services/form.service';

@Component({
  selector: 'app-req-form',
  templateUrl: './req-form.component.html',
  styleUrls: ['./req-form.component.scss']
})
export class ReqFormComponent implements OnInit {

  protected model: any = {};
  protected isLinear = false;
  protected checked = false;
  protected terminal = false;

  constructor(private formService: FormService) { }

  ngOnInit() {
  }

  solicitation() {
    this.formService.create(this.model)
      .subscribe(
        data => {
          console.log(data);
        },
        error => {
          console.log(error);
        });
  }

}



